import React, { useState } from 'react'
import { useNavigate, Link as RouterLink } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import {
  Container,
  Typography,
  Paper,
  TextField,
  Button,
  Box,
  Link,
  Avatar,
  Alert,
  IconButton,
  InputAdornment,
  FormControlLabel,
  Checkbox
} from '@mui/material'
import { PersonAddOutlined, Visibility, VisibilityOff } from '@mui/icons-material'
import { register } from '../store/userSlice'

const RegisterPage = () => {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    password: '',
    confirmPassword: ''
  })
  
  const [showPassword, setShowPassword] = useState(false)
  const [showConfirmPassword, setShowConfirmPassword] = useState(false)
  const [error, setError] = useState('')
  const [loading, setLoading] = useState(false)
  const [termsAccepted, setTermsAccepted] = useState(false)

  // 
  const handleChange = (e) => {
    const { name, value } = e.target
    setFormData(prev => ({
      ...prev,
      [name]: value
    }))
    setError('')
  }

  // 
  const handleTermsChange = (e) => {
    setTermsAccepted(e.target.checked)
    setError('')
  }

  // 
  const isValidEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  }

  // 
  const getPasswordStrength = (password) => {
    if (password.length < 6) {
      return 'weak';
    }
    if (/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{6,}$/.test(password)) {
      return 'strong';
    }
    return 'medium';
  }

  // 
  const handleSubmit = async (e) => {
    e.preventDefault()
    
    // 
    if (!formData.username) {
      setError('')
      return
    }
    
    if (formData.username.length < 3) {
      setError('3')
      return
    }
    
    if (!formData.email) {
      setError('')
      return
    }
    
    if (!isValidEmail(formData.email)) {
      setError('')
      return
    }
    
    if (!formData.password) {
      setError('')
      return
    }
    
    if (formData.password.length < 6) {
      setError('6')
      return
    }
    
    if (getPasswordStrength(formData.password) === 'weak') {
      setError('')
      return
    }
    
    if (!formData.confirmPassword) {
      setError('')
      return
    }
    
    if (formData.password !== formData.confirmPassword) {
      setError('')
      return
    }

    if (!termsAccepted) {
      setError('')
      return
    }

    setLoading(true)
    setError('')
    
    try {
      // Redux action
      await dispatch(register(formData)).unwrap()
      // 
      navigate('/')
    } catch (err) {
      setError(err.message || '')
    } finally {
      setLoading(false)
    }
  }

  // 
  const handleTogglePasswordVisibility = () => {
    setShowPassword(prev => !prev)
  }

  // 
  const handleToggleConfirmPasswordVisibility = () => {
    setShowConfirmPassword(prev => !prev)
  }

  return (
    <Container component="main" maxWidth="xs" sx={{ mt: 8, mb: 8 }}>
      <Paper elevation={3} sx={{ p: 4, borderRadius: 2 }}>
        <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', mb: 3 }}>
          <Avatar sx={{ m: 1, bgcolor: '#6366f1' }}>
            <PersonAddOutlined />
          </Avatar>
          <Typography component="h1" variant="h5" sx={{ fontWeight: 'bold' }}>
            
          </Typography>
          <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
            
          </Typography>
        </Box>

        {error && (
          <Alert severity="error" sx={{ mb: 2 }}>
            {error}
          </Alert>
        )}

        <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
          <TextField
            margin="normal"
            required
            fullWidth
            id="username"
            label=""
            name="username"
            autoComplete="username"
            autoFocus
            value={formData.username}
            onChange={handleChange}
            sx={{
              '& .MuiOutlinedInput-root': {
                borderRadius: 1
              }
            }}
          />
          <TextField
            margin="normal"
            required
            fullWidth
            id="email"
            label=""
            name="email"
            autoComplete="email"
            value={formData.email}
            onChange={handleChange}
            sx={{
              '& .MuiOutlinedInput-root': {
                borderRadius: 1
              }
            }}
          />
          <TextField
            margin="normal"
            required
            fullWidth
            name="password"
            label=""
            type={showPassword ? 'text' : 'password'}
            id="password"
            autoComplete="new-password"
            value={formData.password}
            onChange={handleChange}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={handleTogglePasswordVisibility}
                    edge="end"
                  >
                    {showPassword ? <VisibilityOff /> : <Visibility />}
                  </IconButton>
                </InputAdornment>
              ),
            }}
            sx={{
              '& .MuiOutlinedInput-root': {
                borderRadius: 1
              }
            }}
          />
          <TextField
            margin="normal"
            required
            fullWidth
            name="confirmPassword"
            label=""
            type={showConfirmPassword ? 'text' : 'password'}
            id="confirmPassword"
            autoComplete="new-password"
            value={formData.confirmPassword}
            onChange={handleChange}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle confirm password visibility"
                    onClick={handleToggleConfirmPasswordVisibility}
                    edge="end"
                  >
                    {showConfirmPassword ? <VisibilityOff /> : <Visibility />}
                  </IconButton>
                </InputAdornment>
              ),
            }}
            sx={{
              '& .MuiOutlinedInput-root': {
                borderRadius: 1
              }
            }}
          />

          <FormControlLabel
            control={<Checkbox color="primary" checked={termsAccepted} onChange={handleTermsChange} />}
            label={
              <Typography variant="body2" sx={{ color: 'text.secondary' }}>
                
                <Link component="button" variant="body2" sx={{ mx: 0.5, cursor: 'pointer', color: '#6366f1' }}>
                  
                </Link>
                
                <Link component="button" variant="body2" sx={{ mx: 0.5, cursor: 'pointer', color: '#6366f1' }}>
                  
                </Link>
              </Typography>
            }
            sx={{ mt: 1, mb: 2 }}
          />

          <Button
            type="submit"
            fullWidth
            variant="contained"
            sx={{
              mt: 2,
              mb: 2,
              py: 1.5,
              bgcolor: '#6366f1',
              '&:hover': { bgcolor: '#4f46e5' },
              borderRadius: 1
            }}
            disabled={loading}
          >
            {loading ? '...' : ''}
          </Button>

          <Box sx={{ display: 'flex', justifyContent: 'center' }}>
            <Typography variant="body2" color="text.secondary">
              
              <Link component={RouterLink} to="/login" variant="body2" sx={{ ml: 0.5, color: '#6366f1', textDecoration: 'none' }}>
                
              </Link>
            </Typography>
          </Box>

          <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
            <Typography variant="body2" color="text.secondary">
              
            </Typography>
          </Box>
        </Box>
      </Paper>
    </Container>
  )
}

export default RegisterPage