import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import {
  Container,
  Typography,
  Box,
  Grid,
  Paper,
  Button,
  Divider,
  Alert,
  Radio,
  RadioGroup,
  FormControlLabel,
  FormControl,
  FormLabel,
  TextField,
  CircularProgress,
  Snackbar
} from '@mui/material'
import ArrowBackIcon from '@mui/icons-material/ArrowBack'
import CreditCardIcon from '@mui/icons-material/CreditCard'
import AccountBalanceIcon from '@mui/icons-material/AccountBalance'
import WalletIcon from '@mui/icons-material/Wallet'
import { createPaymentIntent } from '../store/paymentSlice'

const CheckoutPage = () => {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  
  const { items, totalItems, totalAmount } = useSelector((state) => state.cart)
  const { user } = useSelector((state) => state.user)
  
  const [paymentMethod, setPaymentMethod] = useState('credit_card')
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [snackbarOpen, setSnackbarOpen] = useState(false)
  const [snackbarMessage, setSnackbarMessage] = useState('')
  
  // 
  const [formData, setFormData] = useState({
    fullName: user?.username || '',
    email: user?.email || '',
    address: '',
    city: '',
    zipCode: '',
    phone: ''
  })

  // 
  const handleBack = () => {
    navigate('/cart')
  }

  // 
  const handlePaymentMethodChange = (event) => {
    setPaymentMethod(event.target.value)
  }

  // 
  const handleInputChange = (event) => {
    const { name, value } = event.target
    setFormData({
      ...formData,
      [name]: value
    })
  }

  // 
  const handleSubmit = async (event) => {
    event.preventDefault()
    
    // 
    if (!formData.fullName || !formData.email || !formData.address || !formData.city || !formData.zipCode || !formData.phone) {
      setError('')
      return
    }
    
    if (totalItems === 0) {
      setError('')
      return
    }
    
    setLoading(true)
    setError('')
    
    try {
      // API
      await dispatch(createPaymentIntent({ gameIds: items.map(item => item.id) })).unwrap()
      
      // 
      setSnackbarMessage('')
      setSnackbarOpen(true)
      
      setTimeout(() => {
        navigate('/orders')
      }, 1500)
    } catch (err) {
      setError(err.message || '')
    } finally {
      setLoading(false)
    }
  }

  // 
  const handleCloseSnackbar = () => {
    setSnackbarOpen(false)
  }

  // 
  if (!user) {
    navigate('/login')
    return null
  }

  return (
    <Container maxWidth="xl" sx={{ py: 4 }}>
      {/*  */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 4 }}>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
          <Button
            startIcon={<ArrowBackIcon />}
            variant="outlined"
            onClick={handleBack}
          >
            
          </Button>
          <Typography variant="h4" component="h1" sx={{ fontWeight: 'bold' }}>
            
          </Typography>
        </Box>
      </Box>

      {/*  */}
      {error && (
        <Alert severity="error" sx={{ mb: 4 }}>
          {error}
        </Alert>
      )}

      <Grid container spacing={4}>
        {/*  */}
        <Grid item xs={12} md={8}>
          <Paper sx={{ p: 3, borderRadius: 2, mb: 4 }}>
            <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 3 }}>
              
            </Typography>
            
            <form onSubmit={handleSubmit}>
              <Grid container spacing={3}>
                {/*  */}
                <Grid item xs={12} md={6}>
                  <TextField
                    fullWidth
                    label=""
                    name="fullName"
                    value={formData.fullName}
                    onChange={handleInputChange}
                    required
                    sx={{ '& .MuiOutlinedInput-root': { borderRadius: 1 } }}
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <TextField
                    fullWidth
                    label=""
                    name="email"
                    type="email"
                    value={formData.email}
                    onChange={handleInputChange}
                    required
                    sx={{ '& .MuiOutlinedInput-root': { borderRadius: 1 } }}
                  />
                </Grid>
                
                {/*  */}
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label=""
                    name="address"
                    multiline
                    rows={2}
                    value={formData.address}
                    onChange={handleInputChange}
                    required
                    sx={{ '& .MuiOutlinedInput-root': { borderRadius: 1 } }}
                  />
                </Grid>
                
                {/*  */}
                <Grid item xs={12} md={6}>
                  <TextField
                    fullWidth
                    label=""
                    name="city"
                    value={formData.city}
                    onChange={handleInputChange}
                    required
                    sx={{ '& .MuiOutlinedInput-root': { borderRadius: 1 } }}
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <TextField
                    fullWidth
                    label=""
                    name="zipCode"
                    value={formData.zipCode}
                    onChange={handleInputChange}
                    required
                    sx={{ '& .MuiOutlinedInput-root': { borderRadius: 1 } }}
                  />
                </Grid>
                
                {/*  */}
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label=""
                    name="phone"
                    value={formData.phone}
                    onChange={handleInputChange}
                    required
                    sx={{ '& .MuiOutlinedInput-root': { borderRadius: 1 } }}
                  />
                </Grid>
              </Grid>
            </form>
          </Paper>
          
          {/*  */}
          <Paper sx={{ p: 3, borderRadius: 2 }}>
            <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 3 }}>
              
            </Typography>
            
            <FormControl component="fieldset">
              <RadioGroup
                row
                aria-label="payment method"
                name="paymentMethod"
                value={paymentMethod}
                onChange={handlePaymentMethodChange}
              >
                <FormControlLabel
                  value="credit_card"
                  control={<Radio />}
                  label={
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      <CreditCardIcon /> /
                    </Box>
                  }
                />
                <FormControlLabel
                  value="bank_transfer"
                  control={<Radio />}
                  label={
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      <AccountBalanceIcon /> 
                    </Box>
                  }
                />
                <FormControlLabel
                  value="wallet"
                  control={<Radio />}
                  label={
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      <WalletIcon /> 
                    </Box>
                  }
                />
              </RadioGroup>
            </FormControl>
            
            {/*  */}
            <Box sx={{ mt: 3 }}>
              {paymentMethod === 'credit_card' && (
                <Typography variant="body2" color="text.secondary">
                   Visa, MasterCard, American Express 
                </Typography>
              )}
              {paymentMethod === 'bank_transfer' && (
                <Typography variant="body2" color="text.secondary">
                   1-3 
                </Typography>
              )}
              {paymentMethod === 'wallet' && (
                <Typography variant="body2" color="text.secondary">
                  PayPal 
                </Typography>
              )}
            </Box>
          </Paper>
        </Grid>
        
        {/*  */}
        <Grid item xs={12} md={4}>
          <Paper sx={{ p: 3, borderRadius: 2 }}>
            <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 3 }}>
              
            </Typography>
            
            {/*  */}
            <Box sx={{ maxHeight: 300, overflowY: 'auto', mb: 3 }}>
              {items.map((item) => (
                <Box key={item.id} sx={{ display: 'flex', justifyContent: 'space-between', mb: 2, pb: 1, borderBottom: '1px solid rgba(255, 255, 255, 0.1)' }}>
                  <Box sx={{ display: 'flex', gap: 2, alignItems: 'center' }}>
                    <Box sx={{ width: 60, height: 60, borderRadius: 1, overflow: 'hidden', bgcolor: 'rgba(255, 255, 255, 0.1)' }}>
                      <img
                        src={item.image}
                        alt={item.title}
                        style={{ width: '100%', height: '100%', objectFit: 'cover' }}
                      />
                    </Box>
                    <Box>
                      <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                        {item.title}
                      </Typography>
                      <Typography variant="body2" color="text.secondary">
                        {item.quantity}  {item.price.toFixed(2)}
                      </Typography>
                    </Box>
                  </Box>
                  <Typography variant="body2" sx={{ fontWeight: 'bold', color: '#6366f1' }}>
                    {(item.price * item.quantity).toFixed(2)}
                  </Typography>
                </Box>
              ))}
            </Box>
            
            <Divider sx={{ my: 2 }} />
            
            {/*  */}
            <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
              <Typography variant="body1"></Typography>
              <Typography variant="body1">{totalAmount.toFixed(2)}</Typography>
            </Box>
            
            <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
              <Typography variant="body1"></Typography>
              <Typography variant="body1" color="success.main"></Typography>
            </Box>
            
            <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
              <Typography variant="body1"></Typography>
              <Typography variant="body1">{(totalAmount * 0.06).toFixed(2)}</Typography>
            </Box>
            
            <Divider sx={{ my: 2 }} />
            
            <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 3 }}>
              <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                
              </Typography>
              <Typography variant="h6" sx={{ fontWeight: 'bold', color: '#6366f1' }}>
                {(totalAmount * 1.06).toFixed(2)}
              </Typography>
            </Box>
            
            {/*  */}
            <Button
              type="submit"
              variant="contained"
              fullWidth
              size="large"
              sx={{
                bgcolor: '#6366f1',
                '&:hover': { bgcolor: '#4f46e5' },
                py: 1.5,
                borderRadius: 1
              }}
              disabled={loading || totalItems === 0}
              onClick={handleSubmit}
            >
              {loading ? <CircularProgress size={24} color="inherit" /> : ''}
            </Button>
          </Paper>
        </Grid>
      </Grid>
      
      {/*  */}
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={3000}
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert
          onClose={handleCloseSnackbar}
          severity="success"
          sx={{ width: '100%' }}
        >
          {snackbarMessage}
        </Alert>
      </Snackbar>
    </Container>
  )
}

export default CheckoutPage
