import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import {
  Container,
  Typography,
  Box,
  Grid,
  Paper,
  CircularProgress,
  Alert,
  Button,
  Chip,
  Tabs,
  Tab
} from '@mui/material'
import DownloadIcon from '@mui/icons-material/Download'
import PlayArrowIcon from '@mui/icons-material/PlayArrow'
import FilterListIcon from '@mui/icons-material/FilterList'
import SearchIcon from '@mui/icons-material/Search'

const LibraryPage = () => {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  
  const { user } = useSelector((state) => state.user)
  
  const [activeTab, setActiveTab] = useState(0)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [games, setGames] = useState([])

  // 
  const mockGames = [
    {
      id: 1,
      title: '2077',
      image: 'https://via.placeholder.com/300x200/6366f1/ffffff?text=Cyberpunk+2077',
      status: 'installed',
      lastPlayed: '2025-12-01',
      playTime: '25h 30m'
    },
    {
      id: 2,
      title: '',
      image: 'https://via.placeholder.com/300x200/ec4899/ffffff?text=Elden+Ring',
      status: 'installed',
      lastPlayed: '2025-11-28',
      playTime: '45h 15m'
    },
    {
      id: 3,
      title: '',
      image: 'https://via.placeholder.com/300x200/10b981/ffffff?text=Star+Rail',
      status: 'downloaded',
      lastPlayed: '2025-11-25',
      playTime: '15h 45m'
    }
  ]

  useEffect(() => {
    // 
    if (!user) {
      navigate('/login')
      return
    }

    // 
    setLoading(true)
    setTimeout(() => {
      setGames(mockGames)
      setLoading(false)
    }, 1000)
  }, [user, navigate])

  if (!user) {
    return null
  }

  return (
    <Container maxWidth="xl" sx={{ py: 4 }}>
      <Typography variant="h4" component="h1" gutterBottom sx={{ fontWeight: 'bold', mb: 4 }}>
        
      </Typography>

      {/*  */}
      <Paper sx={{ mb: 4, borderRadius: 2, overflow: 'hidden' }}>
        <Tabs
          value={activeTab}
          onChange={(e, newValue) => setActiveTab(newValue)}
          variant="fullWidth"
          textColor="primary"
          indicatorColor="primary"
        >
          <Tab label="" />
          <Tab label="" />
          <Tab label="" />
        </Tabs>
      </Paper>

      {/*  */}
      {loading ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: 400 }}>
          <CircularProgress size={60} />
        </Box>
      ) : error ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: 400 }}>
          <Alert severity="error" sx={{ width: '100%', maxWidth: 500 }}>
            {error}
          </Alert>
        </Box>
      ) : games.length === 0 ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: 400, textAlign: 'center' }}>
          <Box>
            <Typography variant="h6" component="h2" sx={{ mb: 2 }}>
              
            </Typography>
            <Typography variant="body1" sx={{ color: 'text.secondary', mb: 4 }}>
              
            </Typography>
            <Button
              variant="contained"
              sx={{ bgcolor: '#6366f1', '&:hover': { bgcolor: '#4f46e5' }, px: 4 }}
              onClick={() => navigate('/games')}
            >
              
            </Button>
          </Box>
        </Box>
      ) : (
        <Grid container spacing={3}>
          {games.map((game) => (
            <Grid item xs={12} sm={6} md={4} key={game.id}>
              <Paper
                sx={{
                  borderRadius: 2,
                  overflow: 'hidden',
                  transition: 'transform 0.3s, box-shadow 0.3s',
                  '&:hover': {
                    transform: 'translateY(-5px)',
                    boxShadow: '0 10px 25px rgba(0, 0, 0, 0.3)'
                  }
                }}
              >
                {/*  */}
                <Box sx={{ position: 'relative' }}>
                  <img
                    src={game.image}
                    alt={game.title}
                    style={{
                      width: '100%',
                      height: 180,
                      objectFit: 'cover'
                    }}
                  />
                  {/*  */}
                  <Chip
                    label={game.status === 'installed' ? '' : ''}
                    color={game.status === 'installed' ? 'success' : 'primary'}
                    size="small"
                    sx={{
                      position: 'absolute',
                      top: 8,
                      right: 8,
                      fontWeight: 'bold'
                    }}
                  />
                </Box>

                {/*  */}
                <Box sx={{ p: 3 }}>
                  <Typography variant="h6" component="div" sx={{ fontWeight: 'bold', mb: 1 }}>
                    {game.title}
                  </Typography>

                  <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                    <Typography variant="body2" color="text.secondary">
                      : {game.lastPlayed}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      : {game.playTime}
                    </Typography>
                  </Box>

                  {/*  */}
                  <Box sx={{ display: 'flex', gap: 1 }}>
                    {game.status === 'installed' ? (
                      <Button
                        variant="contained"
                        fullWidth
                        startIcon={<PlayArrowIcon />}
                        sx={{ bgcolor: '#6366f1', '&:hover': { bgcolor: '#4f46e5' }, borderRadius: 1.5 }}
                      >
                        
                      </Button>
                    ) : (
                      <Button
                        variant="contained"
                        fullWidth
                        startIcon={<DownloadIcon />}
                        sx={{ bgcolor: '#6366f1', '&:hover': { bgcolor: '#4f46e5' }, borderRadius: 1.5 }}
                      >
                        
                      </Button>
                    )}
                  </Box>
                </Box>
              </Paper>
            </Grid>
          ))}
        </Grid>
      )}
    </Container>
  )
}

export default LibraryPage
