import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import api from '../utils/api'

// Thunk - 
export const createPaymentIntent = createAsyncThunk(
  'payment/createPaymentIntent',
  async ({ gameIds }, { rejectWithValue }) => {
    try {
      const response = await api.post('/payments/intent', { gameIds })
      return response.data
    } catch (error) {
      return rejectWithValue(error.response?.data?.message || '')
    }
  }
)

// Thunk - 
export const confirmPayment = createAsyncThunk(
  'payment/confirmPayment',
  async ({ orderId, paymentIntentId }, { rejectWithValue }) => {
    try {
      const response = await api.post('/payments/confirm', { orderId, paymentIntentId })
      return response.data
    } catch (error) {
      return rejectWithValue(error.response?.data?.message || '')
    }
  }
)

// Thunk - 
export const fetchOrderHistory = createAsyncThunk(
  'payment/fetchOrderHistory',
  async ({ page = 1, limit = 10 }, { rejectWithValue }) => {
    try {
      const response = await api.get('/payments/orders', { params: { page, limit } })
      return response.data.data
    } catch (error) {
      return rejectWithValue(error.response?.data?.message || '')
    }
  }
)

// Thunk - 
export const fetchOrderDetail = createAsyncThunk(
  'payment/fetchOrderDetail',
  async (orderId, { rejectWithValue }) => {
    try {
      const response = await api.get(`/payments/orders/${orderId}`)
      return response.data.data
    } catch (error) {
      return rejectWithValue(error.response?.data?.message || '')
    }
  }
)

// 
const initialState = {
  // 
  loading: false,
  paymentIntent: null,
  paymentSuccess: false,
  order: null,
  // 
  orders: [],
  totalPages: 1,
  currentPage: 1,
  // 
  error: null
}

// paymentSlice
const paymentSlice = createSlice({
  name: 'payment',
  initialState,
  reducers: {
    // 
    resetPayment: (state) => {
      state.paymentIntent = null
      state.paymentSuccess = false
      state.error = null
    },
    // 
    clearError: (state) => {
      state.error = null
    }
  },
  extraReducers: (builder) => {
    builder
      // 
      .addCase(createPaymentIntent.pending, (state) => {
        state.loading = true
        state.error = null
      })
      .addCase(createPaymentIntent.fulfilled, (state, action) => {
        state.loading = false
        state.paymentIntent = action.payload
      })
      .addCase(createPaymentIntent.rejected, (state, action) => {
        state.loading = false
        state.error = action.payload
      })
      
      // 
      .addCase(confirmPayment.pending, (state) => {
        state.loading = true
        state.error = null
      })
      .addCase(confirmPayment.fulfilled, (state, action) => {
        state.loading = false
        state.paymentSuccess = true
        state.order = action.payload.order
      })
      .addCase(confirmPayment.rejected, (state, action) => {
        state.loading = false
        state.error = action.payload
      })
      
      // 
      .addCase(fetchOrderHistory.pending, (state) => {
        state.loading = true
        state.error = null
      })
      .addCase(fetchOrderHistory.fulfilled, (state, action) => {
        state.loading = false
        state.orders = action.payload.orders
        state.totalPages = action.payload.pagination.totalPages
        state.currentPage = action.payload.pagination.currentPage
      })
      .addCase(fetchOrderHistory.rejected, (state, action) => {
        state.loading = false
        state.error = action.payload
      })
      
      // 
      .addCase(fetchOrderDetail.pending, (state) => {
        state.loading = true
        state.error = null
      })
      .addCase(fetchOrderDetail.fulfilled, (state, action) => {
        state.loading = false
        state.order = action.payload
      })
      .addCase(fetchOrderDetail.rejected, (state, action) => {
        state.loading = false
        state.error = action.payload
      })
  }
})

// action creators
export const { resetPayment, clearError } = paymentSlice.actions

// reducer
export default paymentSlice.reducer