import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import {
  Container,
  Typography,
  Paper,
  TextField,
  Button,
  Box,
  Divider,
  Alert,
  Avatar,
  IconButton,
  InputAdornment,
  Tabs,
  Tab,
  Grid
} from '@mui/material'
import { Visibility, VisibilityOff, Edit, Save, Cancel } from '@mui/icons-material'
import { updateUserProfile, changePassword } from '../store/userSlice'

const ProfilePage = () => {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const { user, loading } = useSelector((state) => state.user)
  
  // 
  const [activeTab, setActiveTab] = useState(0)
  
  // 
  const [isEditing, setIsEditing] = useState(false)
  const [profileForm, setProfileForm] = useState({
    username: user?.username || '',
    email: user?.email || ''
  })
  
  // 
  const [passwordForm, setPasswordForm] = useState({
    currentPassword: '',
    newPassword: '',
    confirmNewPassword: ''
  })
  
  // 
  const [showCurrentPassword, setShowCurrentPassword] = useState(false)
  const [showNewPassword, setShowNewPassword] = useState(false)
  const [showConfirmNewPassword, setShowConfirmNewPassword] = useState(false)
  
  // 
  const [message, setMessage] = useState('')
  const [messageSeverity, setMessageSeverity] = useState('success')

  // 
  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue)
    resetForms()
  }

  // 
  const resetForms = () => {
    setIsEditing(false)
    setProfileForm({
      username: user?.username || '',
      email: user?.email || ''
    })
    setPasswordForm({
      currentPassword: '',
      newPassword: '',
      confirmNewPassword: ''
    })
    setMessage('')
  }

  // 
  const handleProfileChange = (e) => {
    const { name, value } = e.target
    setProfileForm(prev => ({
      ...prev,
      [name]: value
    }))
  }

  // 
  const handlePasswordChange = (e) => {
    const { name, value } = e.target
    setPasswordForm(prev => ({
      ...prev,
      [name]: value
    }))
  }

  // 
  const togglePasswordVisibility = (field) => {
    switch (field) {
      case 'current':
        setShowCurrentPassword(prev => !prev)
        break
      case 'new':
        setShowNewPassword(prev => !prev)
        break
      case 'confirm':
        setShowConfirmNewPassword(prev => !prev)
        break
      default:
        break
    }
  }

  // 
  const handleStartEdit = () => {
    setIsEditing(true)
    setMessage('')
  }

  // 
  const handleCancelEdit = () => {
    resetForms()
  }

  // 
  const handleUpdateProfile = async () => {
    if (!profileForm.username || !profileForm.email) {
      setMessage('')
      setMessageSeverity('error')
      return
    }

    try {
      await dispatch(updateUserProfile(profileForm)).unwrap()
      setMessage('')
      setMessageSeverity('success')
      setIsEditing(false)
    } catch (err) {
      setMessage(err.message || '')
      setMessageSeverity('error')
    }
  }

  // 
  const handleChangePassword = async () => {
    if (!passwordForm.currentPassword || !passwordForm.newPassword || !passwordForm.confirmNewPassword) {
      setMessage('')
      setMessageSeverity('error')
      return
    }

    if (passwordForm.newPassword !== passwordForm.confirmNewPassword) {
      setMessage('')
      setMessageSeverity('error')
      return
    }

    if (passwordForm.newPassword.length < 6) {
      setMessage('6')
      setMessageSeverity('error')
      return
    }

    try {
      await dispatch(changePassword({
        currentPassword: passwordForm.currentPassword,
        newPassword: passwordForm.newPassword
      })).unwrap()
      setMessage('')
      setMessageSeverity('success')
      setPasswordForm({
        currentPassword: '',
        newPassword: '',
        confirmNewPassword: ''
      })
    } catch (err) {
      setMessage(err.message || '')
      setMessageSeverity('error')
    }
  }

  // 
  if (!user && !loading) {
    navigate('/login')
    return null
  }

  return (
    <Container component="main" maxWidth="md" sx={{ mt: 8, mb: 8 }}>
      <Typography variant="h4" component="h1" gutterBottom sx={{ fontWeight: 'bold', mb: 4 }}>
        
      </Typography>

      <Paper elevation={3} sx={{ p: 4, borderRadius: 2 }}>
        {/*  */}
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 3, mb: 4 }}>
          <Avatar sx={{ width: 100, height: 100, bgcolor: '#6366f1', fontSize: '2rem' }}>
            {user?.username?.charAt(0).toUpperCase() || 'U'}
          </Avatar>
          <Box>
            <Typography variant="h5" component="div" sx={{ fontWeight: 'bold' }}>
              {user?.username}
            </Typography>
            <Typography variant="body1" color="text.secondary">
              {user?.email}
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
              : {user?.created_at ? new Date(user.created_at).toLocaleDateString('zh-CN') : ''}
            </Typography>
          </Box>
        </Box>

        <Divider sx={{ mb: 4 }} />

        {/*  */}
        <Tabs value={activeTab} onChange={handleTabChange} sx={{ mb: 3 }}>
          <Tab label="" />
          <Tab label="" />
          <Tab label="" />
        </Tabs>

        {/*  */}
        {message && (
          <Alert severity={messageSeverity} sx={{ mb: 3 }}>
            {message}
          </Alert>
        )}

        {/*  */}
        {activeTab === 0 && (
          <Box>
            <Grid container spacing={3}>
              <Grid item xs={12} sm={6}>
                <TextField
                  fullWidth
                  id="username"
                  label=""
                  name="username"
                  value={profileForm.username}
                  onChange={handleProfileChange}
                  disabled={!isEditing}
                  InputProps={{
                    endAdornment: !isEditing && (
                      <InputAdornment position="end">
                        <IconButton onClick={handleStartEdit} title="">
                          <Edit />
                        </IconButton>
                      </InputAdornment>
                    )
                  }}
                  sx={{
                    '& .MuiOutlinedInput-root': {
                      borderRadius: 1
                    }
                  }}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  fullWidth
                  id="email"
                  label=""
                  name="email"
                  type="email"
                  value={profileForm.email}
                  onChange={handleProfileChange}
                  disabled={!isEditing}
                  sx={{
                    '& .MuiOutlinedInput-root': {
                      borderRadius: 1
                    }
                  }}
                />
              </Grid>
            </Grid>

            {isEditing && (
              <Box sx={{ display: 'flex', gap: 2, mt: 3, justifyContent: 'flex-end' }}>
                <Button
                  variant="outlined"
                  onClick={handleCancelEdit}
                  startIcon={<Cancel />}
                  sx={{ borderRadius: 1 }}
                >
                  
                </Button>
                <Button
                  variant="contained"
                  onClick={handleUpdateProfile}
                  startIcon={<Save />}
                  sx={{
                    borderRadius: 1,
                    bgcolor: '#6366f1',
                    '&:hover': { bgcolor: '#4f46e5' }
                  }}
                >
                  
                </Button>
              </Box>
            )}
          </Box>
        )}

        {/*  */}
        {activeTab === 1 && (
          <Box>
            <Grid container spacing={3}>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  id="currentPassword"
                  label=""
                  name="currentPassword"
                  type={showCurrentPassword ? 'text' : 'password'}
                  value={passwordForm.currentPassword}
                  onChange={handlePasswordChange}
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton onClick={() => togglePasswordVisibility('current')}>
                          {showCurrentPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    )
                  }}
                  sx={{
                    '& .MuiOutlinedInput-root': {
                      borderRadius: 1
                    }
                  }}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  id="newPassword"
                  label=""
                  name="newPassword"
                  type={showNewPassword ? 'text' : 'password'}
                  value={passwordForm.newPassword}
                  onChange={handlePasswordChange}
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton onClick={() => togglePasswordVisibility('new')}>
                          {showNewPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    )
                  }}
                  sx={{
                    '& .MuiOutlinedInput-root': {
                      borderRadius: 1
                    }
                  }}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  id="confirmNewPassword"
                  label=""
                  name="confirmNewPassword"
                  type={showConfirmNewPassword ? 'text' : 'password'}
                  value={passwordForm.confirmNewPassword}
                  onChange={handlePasswordChange}
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton onClick={() => togglePasswordVisibility('confirm')}>
                          {showConfirmNewPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    )
                  }}
                  sx={{
                    '& .MuiOutlinedInput-root': {
                      borderRadius: 1
                    }
                  }}
                />
              </Grid>
            </Grid>

            <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 3 }}>
              <Button
                variant="contained"
                onClick={handleChangePassword}
                sx={{
                  borderRadius: 1,
                  bgcolor: '#6366f1',
                  '&:hover': { bgcolor: '#4f46e5' },
                  py: 1.2
                }}
              >
                
              </Button>
            </Box>
          </Box>
        )}

        {/*  */}
        {activeTab === 2 && (
          <Box>
            <Typography variant="body1" color="text.secondary" sx={{ mb: 4 }}>
              ...
            </Typography>
            <Grid container spacing={3}>
              <Grid item xs={12} sm={6}>
                <Paper elevation={2} sx={{ p: 3, borderRadius: 2, textAlign: 'center' }}>
                  <Typography variant="h6" component="div" sx={{ mb: 2, fontWeight: 'bold' }}>
                    
                  </Typography>
                  <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>
                    
                  </Typography>
                  <Button
                    variant="outlined"
                    disabled
                    sx={{ borderRadius: 1 }}
                  >
                    
                  </Button>
                </Paper>
              </Grid>
              <Grid item xs={12} sm={6}>
                <Paper elevation={2} sx={{ p: 3, borderRadius: 2, textAlign: 'center' }}>
                  <Typography variant="h6" component="div" sx={{ mb: 2, fontWeight: 'bold' }}>
                    
                  </Typography>
                  <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>
                    
                  </Typography>
                  <Button
                    variant="outlined"
                    disabled
                    sx={{ borderRadius: 1 }}
                  >
                    
                  </Button>
                </Paper>
              </Grid>
            </Grid>
          </Box>
        )}
      </Paper>
    </Container>
  )
}

export default ProfilePage