      { id: 2, title: '', sales: 280, revenue: 55720, price: 199 },
      { id: 3, title: '', sales: 250, revenue: 67000, price: 268 },
      { id: 4, title: '', sales: 180, revenue: 0, price: 0 },
      { id: 5, title: '', sales: 120, revenue: 29880, price: 249 }
