      { region: '', sales: 350, revenue: 104300, percentage: 28 },
      { region: '', sales: 280, revenue: 83200, percentage: 22 },
      { region: '', sales: 240, revenue: 71520, percentage: 18 },
      { region: '', sales: 180, revenue: 53640, percentage: 14 },
      { region: '', sales: 120, revenue: 35760, percentage: 9 },
      { region: '', sales: 80, revenue: 23840, percentage: 7 }
