import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import {
  Container,
  Typography,
  Box,
  Paper,
  CircularProgress,
  Alert,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Chip,
  Button,
  Tabs,
  Tab,
  Pagination
} from '@mui/material'
import { fetchOrderHistory } from '../store/paymentSlice'
import ReceiptLongIcon from '@mui/icons-material/ReceiptLong'
import CalendarTodayIcon from '@mui/icons-material/CalendarToday'
import PaidIcon from '@mui/icons-material/Paid'
import LocalShippingIcon from '@mui/icons-material/LocalShipping'

const OrderHistoryPage = () => {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  
  const { user } = useSelector((state) => state.user)
  const { orders, loading, error, totalPages, currentPage } = useSelector((state) => state.payment)
  
  const [activeTab, setActiveTab] = useState(0)
  const [filter, setFilter] = useState('all')

  // 
  const mockOrders = [
    {
      id: 1,
      orderNumber: 'MUYU-20251201-001',
      totalAmount: 298.00,
      status: 'completed',
      createdAt: '2025-12-01T10:30:00Z',
      games: [
        { id: 1, title: '2077', price: 298.00 }
      ]
    },
    {
      id: 2,
      orderNumber: 'MUYU-20251128-002',
      totalAmount: 199.00,
      status: 'completed',
      createdAt: '2025-11-28T15:45:00Z',
      games: [
        { id: 2, title: '', price: 199.00 }
      ]
    },
    {
      id: 3,
      orderNumber: 'MUYU-20251125-003',
      totalAmount: 0.00,
      status: 'completed',
      createdAt: '2025-11-25T09:15:00Z',
      games: [
        { id: 3, title: '', price: 0.00 }
      ]
    }
  ]

  useEffect(() => {
    // 
    if (!user) {
      navigate('/login')
      return
    }

    // 
    // dispatch(fetchOrderHistory({ page: 1, limit: 10 }))
  }, [user, navigate, dispatch])

  // 
  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue)
    switch (newValue) {
      case 0:
        setFilter('all')
        break
      case 1:
        setFilter('completed')
        break
      case 2:
        setFilter('pending')
        break
      case 3:
        setFilter('cancelled')
        break
      default:
        setFilter('all')
    }
  }

  // 
  const handlePageChange = (event, page) => {
    // dispatch(fetchOrderHistory({ page, limit: 10, filter }))
  }

  // 
  const handleViewOrder = (orderId) => {
    // navigate(`/orders/${orderId}`)
    alert(`: ${orderId}`)
  }

  // 
  const formatDate = (dateString) => {
    const date = new Date(dateString)
    return date.toLocaleString('zh-CN', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit'
    })
  }

  // 
  const getOrderStatusLabel = (status) => {
    switch (status) {
      case 'completed':
        return { label: '', color: 'success' }
      case 'pending':
        return { label: '', color: 'warning' }
      case 'processing':
        return { label: '', color: 'info' }
      case 'shipped':
        return { label: '', color: 'primary' }
      case 'cancelled':
        return { label: '', color: 'error' }
      default:
        return { label: '', color: 'default' }
    }
  }

  if (!user) {
    return null
  }

  return (
    <Container maxWidth="xl" sx={{ py: 4 }}>
      {/*  */}
      <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 4 }}>
        <ReceiptLongIcon sx={{ fontSize: 32, color: '#6366f1' }} />
        <Typography variant="h4" component="h1" sx={{ fontWeight: 'bold' }}>
          
        </Typography>
      </Box>

      {/*  */}
      <Paper sx={{ mb: 4, borderRadius: 2, overflow: 'hidden' }}>
        <Tabs
          value={activeTab}
          onChange={handleTabChange}
          variant="fullWidth"
          textColor="primary"
          indicatorColor="primary"
        >
          <Tab label="" />
          <Tab label="" />
          <Tab label="" />
          <Tab label="" />
        </Tabs>
      </Paper>

      {/*  */}
      {loading ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: 400 }}>
          <CircularProgress size={60} />
        </Box>
      ) : error ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: 400 }}>
          <Alert severity="error" sx={{ width: '100%', maxWidth: 500 }}>
            {error}
          </Alert>
        </Box>
      ) : mockOrders.length === 0 ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: 400, textAlign: 'center' }}>
          <Box>
            <Typography variant="h6" component="h2" sx={{ mb: 2 }}>
              
            </Typography>
            <Typography variant="body1" sx={{ color: 'text.secondary', mb: 4 }}>
              
            </Typography>
            <Button
              variant="contained"
              sx={{ bgcolor: '#6366f1', '&:hover': { bgcolor: '#4f46e5' }, px: 4 }}
              onClick={() => navigate('/games')}
            >
              
            </Button>
          </Box>
        </Box>
      ) : (
        <Box>
          <TableContainer component={Paper} sx={{ borderRadius: 2, mb: 3 }}>
            <Table aria-label="">
              <TableHead>
                <TableRow>
                  <TableCell sx={{ fontWeight: 'bold', fontSize: '1rem' }}></TableCell>
                  <TableCell sx={{ fontWeight: 'bold', fontSize: '1rem' }}></TableCell>
                  <TableCell sx={{ fontWeight: 'bold', fontSize: '1rem' }}></TableCell>
                  <TableCell sx={{ fontWeight: 'bold', fontSize: '1rem' }}></TableCell>
                  <TableCell sx={{ fontWeight: 'bold', fontSize: '1rem' }}></TableCell>
                  <TableCell sx={{ fontWeight: 'bold', fontSize: '1rem', textAlign: 'right' }}></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {mockOrders.map((order) => {
                  const statusLabel = getOrderStatusLabel(order.status)
                  return (
                    <TableRow
                      key={order.id}
                      sx={{
                        '&:last-child td, &:last-child th': { border: 0 },
                        '&:hover': { backgroundColor: 'rgba(255, 255, 255, 0.04)' }
                      }}
                    >
                      <TableCell>
                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                          <ReceiptLongIcon sx={{ fontSize: 16, color: '#6366f1' }} />
                          <Typography variant="body1" sx={{ fontWeight: 'bold' }}>
                            {order.orderNumber}
                          </Typography>
                        </Box>
                      </TableCell>
                      <TableCell>
                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                          <CalendarTodayIcon sx={{ fontSize: 16, color: '#6366f1' }} />
                          <Typography variant="body2">
                            {formatDate(order.createdAt)}
                          </Typography>
                        </Box>
                      </TableCell>
                      <TableCell>
                        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1 }}>
                          {order.games.map((game) => (
                            <Typography key={game.id} variant="body2">
                              {game.title}
                            </Typography>
                          ))}
                        </Box>
                      </TableCell>
                      <TableCell>
                        <Chip
                          label={statusLabel.label}
                          color={statusLabel.color}
                          size="small"
                          variant="outlined"
                        />
                      </TableCell>
                      <TableCell>
                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                          <PaidIcon sx={{ fontSize: 16, color: '#6366f1' }} />
                          <Typography variant="body1" sx={{ fontWeight: 'bold', color: '#6366f1' }}>
                            {order.totalAmount.toFixed(2)}
                          </Typography>
                        </Box>
                      </TableCell>
                      <TableCell align="right">
                        <Button
                          variant="outlined"
                          size="small"
                          onClick={() => handleViewOrder(order.id)}
                        >
                          
                        </Button>
                      </TableCell>
                    </TableRow>
                  )
                })}
              </TableBody>
            </Table>
          </TableContainer>
          
          {/*  */}
          <Box sx={{ display: 'flex', justifyContent: 'center', mt: 3 }}>
            <Pagination
              count={totalPages}
              page={currentPage}
              onChange={handlePageChange}
              color="primary"
              sx={{ mb: 3 }}
            />
          </Box>
        </Box>
      )}
    </Container>
  )
}

export default OrderHistoryPage
