import React from 'react'
import { useNavigate } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import {
  Container,
  Typography,
  Box,
  Grid,
  Paper,
  Button,
  IconButton,
  Divider,
  Alert,
  Snackbar
} from '@mui/material'
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart'
import ArrowBackIcon from '@mui/icons-material/ArrowBack'
import RemoveIcon from '@mui/icons-material/Remove'
import AddIcon from '@mui/icons-material/Add'
import DeleteIcon from '@mui/icons-material/Delete'
import { removeFromCart, updateQuantity, clearCart } from '../store/cartSlice'

const CartPage = () => {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  
  const { items, totalItems, totalAmount } = useSelector((state) => state.cart)
  
  const [snackbarOpen, setSnackbarOpen] = React.useState(false)
  const [snackbarMessage, setSnackbarMessage] = React.useState('')

  // 
  const handleBack = () => {
    navigate(-1)
  }

  // 
  const handleIncreaseQuantity = (item) => {
    dispatch(updateQuantity({
      gameId: item.id,
      quantity: item.quantity + 1
    }))
  }

  // 
  const handleDecreaseQuantity = (item) => {
    if (item.quantity > 1) {
      dispatch(updateQuantity({
        gameId: item.id,
        quantity: item.quantity - 1
      }))
    }
  }

  // 
  const handleRemoveItem = (itemId) => {
    dispatch(removeFromCart({ gameId: itemId }))
    setSnackbarMessage('')
    setSnackbarOpen(true)
  }

  // 
  const handleClearCart = () => {
    if (window.confirm('')) {
      dispatch(clearCart())
      setSnackbarMessage('')
      setSnackbarOpen(true)
    }
  }

  // 
  const handleCheckout = () => {
    if (totalItems === 0) {
      setSnackbarMessage('')
      setSnackbarOpen(true)
      return
    }
    navigate('/checkout')
  }

  // 
  const handleCloseSnackbar = () => {
    setSnackbarOpen(false)
  }

  return (
    <Container maxWidth="xl" sx={{ py: 4 }}>
      {/*  */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 4 }}>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
          <Button
            startIcon={<ArrowBackIcon />}
            variant="outlined"
            onClick={handleBack}
          >
            
          </Button>
          <Typography variant="h4" component="h1" sx={{ fontWeight: 'bold' }}>
            <ShoppingCartIcon sx={{ mr: 1 }} />
            
          </Typography>
        </Box>
        <Typography variant="h6" sx={{ fontWeight: 'bold', color: '#6366f1' }}>
          {totalItems} 
        </Typography>
      </Box>

      {/*  */}
      {totalItems === 0 ? (
        <Paper sx={{ p: 8, textAlign: 'center', borderRadius: 2 }}>
          <ShoppingCartIcon sx={{ fontSize: 120, color: 'text.secondary', mb: 3 }} />
          <Typography variant="h5" component="h2" sx={{ fontWeight: 'bold', mb: 2 }}>
            
          </Typography>
          <Typography variant="body1" color="text.secondary" sx={{ mb: 4 }}>
            
          </Typography>
          <Button
            variant="contained"
            size="large"
            sx={{ bgcolor: '#6366f1', '&:hover': { bgcolor: '#4f46e5' }, px: 4 }}
            onClick={() => navigate('/games')}
          >
            
          </Button>
        </Paper>
      ) : (
        <Grid container spacing={4}>
          {/*  */}
          <Grid item xs={12} md={8}>
            <Paper sx={{ p: 3, borderRadius: 2 }}>
              {/*  */}
              {items.map((item) => (
                <Box key={item.id} sx={{ mb: 3 }}>
                  <Grid container spacing={2} alignItems="center">
                    {/*  */}
                    <Grid item xs={4} md={3}>
                      <Box sx={{ width: '100%', height: 100, borderRadius: 1, overflow: 'hidden', bgcolor: 'rgba(255, 255, 255, 0.1)' }}>
                        <img
                          src={item.image}
                          alt={item.title}
                          style={{ width: '100%', height: '100%', objectFit: 'cover' }}
                        />
                      </Box>
                    </Grid>

                    {/*  */}
                    <Grid item xs={8} md={9}>
                      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
                        <Box>
                          <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 1 }}>
                            {item.title}
                          </Typography>
                          <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                            {item.isFree ? '' : `${item.price.toFixed(2)}`}
                          </Typography>
                          
                          {/*  */}
                          <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
                            <IconButton
                              size="small"
                              onClick={() => handleDecreaseQuantity(item)}
                              disabled={item.quantity <= 1}
                            >
                              <RemoveIcon />
                            </IconButton>
                            <Typography variant="body1" sx={{ fontWeight: 'bold', minWidth: 24, textAlign: 'center' }}>
                              {item.quantity}
                            </Typography>
                            <IconButton
                              size="small"
                              onClick={() => handleIncreaseQuantity(item)}
                            >
                              <AddIcon />
                            </IconButton>
                          </Box>
                        </Box>

                        {/*  */}
                        <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end', gap: 1 }}>
                          {/*  */}
                          <Typography variant="body1" sx={{ fontWeight: 'bold', color: '#6366f1' }}>
                            {(item.price * item.quantity).toFixed(2)}
                          </Typography>
                          {/*  */}
                          <IconButton
                            size="small"
                            color="error"
                            onClick={() => handleRemoveItem(item.id)}
                          >
                            <DeleteIcon />
                          </IconButton>
                        </Box>
                      </Box>
                    </Grid>
                  </Grid>
                  <Divider sx={{ mt: 2 }} />
                </Box>
              ))}

              {/*  */}
              <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 3 }}>
                <Button
                  variant="outlined"
                  color="error"
                  startIcon={<DeleteIcon />}
                  onClick={handleClearCart}
                >
                  
                </Button>
              </Box>
            </Paper>
          </Grid>

          {/*  */}
          <Grid item xs={12} md={4}>
            <Paper sx={{ p: 3, borderRadius: 2 }}>
              <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 3 }}>
                
              </Typography>

              {/*  */}
              <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
                <Typography variant="body1"></Typography>
                <Typography variant="body1">{totalItems} </Typography>
              </Box>

              {/*  */}
              <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
                <Typography variant="body1"></Typography>
                <Typography variant="body1">{totalAmount.toFixed(2)}</Typography>
              </Box>

              {/*  */}
              <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
                <Typography variant="body1"></Typography>
                <Typography variant="body1" color="success.main">
                  
                </Typography>
              </Box>

              <Divider sx={{ my: 2 }} />

              {/*  */}
              <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 3 }}>
                <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                  
                </Typography>
                <Typography variant="h6" sx={{ fontWeight: 'bold', color: '#6366f1' }}>
                  {totalAmount.toFixed(2)}
                </Typography>
              </Box>

              {/*  */}
              <Button
                variant="contained"
                fullWidth
                size="large"
                sx={{ 
                  bgcolor: '#6366f1', 
                  '&:hover': { bgcolor: '#4f46e5' },
                  py: 1.5,
                  fontSize: '1rem'
                }}
                onClick={handleCheckout}
              >
                
              </Button>

              {/*  */}
              <Button
                variant="outlined"
                fullWidth
                sx={{ mt: 2 }}
                onClick={() => navigate('/games')}
              >
                
              </Button>
            </Paper>
          </Grid>
        </Grid>
      )}

      {/*  */}
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={3000}
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert
          onClose={handleCloseSnackbar}
          severity="success"
          sx={{ width: '100%' }}
        >
          {snackbarMessage}
        </Alert>
      </Snackbar>
    </Container>
  )
}

export default CartPage
